import { NextRequest, NextResponse } from "next/server"
import { transports, pickupRequests, activityLogs, users, wasteLogs, notifications } from "@/app/api/_data"
import type { Transport } from "@/app/api/_types"

type CreateTransportBody = {
  pickupRequestId: string
  transportType: "internal" | "external" | "manual"
  provider?: string
  assignedTo?: string
  pickupDate: string
  pickupWindow: {
    from: string
    to: string
  }
  cost?: number
  trackingLink?: string
  notes?: string
}

// GET /api/transports
// Returns all transport records
export async function GET(req: NextRequest) {
  // Get user ID from header (for admin/staff)
  const userId = req.headers.get("x-user-id")
  if (!userId) {
    return NextResponse.json({ error: "Missing user ID" }, { status: 400 })
  }

  const user = users.find((u) => u.id === userId)
  if (!user || (user.role !== "admin" && user.role !== "staff")) {
    return NextResponse.json({ error: "Unauthorized" }, { status: 403 })
  }

  // Filter transports by cafe if user is staff
  let filteredTransports = transports
  if (user.role === "staff" && user.cafeId) {
    filteredTransports = transports.filter((t) => {
      const pickup = pickupRequests.find((p) => p.id === t.pickupRequestId)
      if (!pickup) return false
      const waste = wasteLogs.find((w) => w.id === pickup.wasteId)
      return waste?.cafeId === user.cafeId
    })
  }

  // Enrich with pickup request and waste details
  // Only return transports that are scheduled (pickup-scheduled or scheduled status)
  const enriched = filteredTransports
    .map((t) => {
      const pickup = pickupRequests.find((p) => p.id === t.pickupRequestId)
      const waste = pickup ? wasteLogs.find((w) => w.id === pickup.wasteId) : null
      const requester = pickup ? users.find((u) => u.id === pickup.requesterId) : null
      const assignedUser = t.assignedTo ? users.find((u) => u.id === t.assignedTo) : null

      return {
        ...t,
        pickupRequest: pickup
          ? {
              ...pickup,
              itemName: waste?.itemName,
              quantity: waste?.quantity,
              requesterName: requester?.organizationName || requester?.name,
            }
          : null,
        assignedToName: assignedUser?.name || assignedUser?.organizationName,
      }
    })
    .filter((t) => {
      // Only show scheduled pickups (pickup-scheduled, scheduled, or completed)
      const status = t.status.toLowerCase()
      return status === "scheduled" || status === "pickup-scheduled" || status === "completed"
    })

  return NextResponse.json(enriched)
}

// POST /api/transports
// Creates a new transport record and updates the pickup request
export async function POST(req: NextRequest) {
  const userId = req.headers.get("x-user-id")
  if (!userId) {
    return NextResponse.json({ error: "Missing user ID" }, { status: 400 })
  }

  const user = users.find((u) => u.id === userId)
  if (!user || (user.role !== "admin" && user.role !== "staff")) {
    return NextResponse.json({ error: "Unauthorized" }, { status: 403 })
  }

  const body = (await req.json()) as Partial<CreateTransportBody>

  if (!body.pickupRequestId || !body.transportType || !body.pickupDate || !body.pickupWindow) {
    return NextResponse.json(
      { error: "Missing required fields: pickupRequestId, transportType, pickupDate, pickupWindow" },
      { status: 400 }
    )
  }

  // Verify pickup request exists and is approved
  const pickupRequest = pickupRequests.find((p) => p.id === body.pickupRequestId)
  if (!pickupRequest) {
    return NextResponse.json({ error: "Pickup request not found" }, { status: 404 })
  }

  // Only allow scheduling for approved pickup requests
  if (pickupRequest.status !== "approved") {
    return NextResponse.json(
      { error: `Cannot schedule pickup for request with status "${pickupRequest.status}". Only approved requests can be scheduled.` },
      { status: 400 }
    )
  }

  // Check if transport already exists for this pickup request
  const existingTransport = transports.find((t) => t.pickupRequestId === body.pickupRequestId)
  if (existingTransport) {
    return NextResponse.json({ error: "Transport already exists for this pickup request" }, { status: 400 })
  }

  // Create new transport
  const now = new Date().toISOString()
  const newTransport: Transport = {
    id: `t-${Date.now()}`,
    pickupRequestId: body.pickupRequestId,
    transportType: body.transportType,
    provider: body.provider,
    assignedTo: body.assignedTo,
    pickupDate: body.pickupDate,
    pickupWindow: body.pickupWindow,
    cost: body.cost,
    trackingLink: body.trackingLink,
    notes: body.notes,
    status: "scheduled",
    createdAt: now,
    updatedAt: now,
  }

  transports.push(newTransport)

  // Update pickup request
  pickupRequest.status = "pickup-scheduled"
  pickupRequest.transportId = newTransport.id
  pickupRequest.pickupWindow = {
    date: body.pickupDate,
    from: body.pickupWindow.from,
    to: body.pickupWindow.to,
  }

  // Add activity log
  const waste = wasteLogs.find((w) => w.id === pickupRequest.wasteId)
  activityLogs.push({
    id: `a-${Date.now()}`,
    userId: user.id,
    actionType: "approved", // Using approved as transport assignment
    itemName: waste?.itemName || "Unknown",
    createdAt: now,
    requesterType: pickupRequest.requesterType,
  })

  // Create notification for charity/farmer
  const dateStr = new Date(body.pickupDate).toLocaleDateString("en-GB", {
    day: "2-digit",
    month: "2-digit",
    year: "numeric",
  })
  const timeStr = `${body.pickupWindow.from} – ${body.pickupWindow.to}`
  
  notifications.push({
    id: `n-${Date.now()}`,
    userId: pickupRequest.requesterId,
    type: "pickup-scheduled",
    title: "Pickup Scheduled",
    message: `Your pickup has been scheduled on ${dateStr} from ${timeStr}.`,
    read: false,
    createdAt: now,
    relatedRequestId: pickupRequest.id,
  })

  return NextResponse.json(newTransport, { status: 201 })
}

